:root, [data-theme="default"] {
    /*========== Colors ==========*/
    --color-bg: rgba(255, 255, 255, 1);
    --color-text: rgba(51, 56, 58, 1);
    --color-primary: rgba(31, 166, 157, 1);
    --color-secondary: rgba(29, 211, 176, 1);
    /*========== Font and typography ==========*/
    --font-body: "abel-regular", Arial, "Helvetica CY", "Nimbus Sans L", sans-serif;
    --font-big: "big_john", "Arial Black", "Gadget", "Nimbus Sans L", sans-serif;
    --font-slim: "slim_joe", "Courier New", Courier, "Nimbus Mono L", monospace;
    --font-koh: 'Stick No Bills', sans-serif;

    --font-icon: "weathericons";
    --font-size-base: 16px;
}

[data-theme="warm"] {
    /*========== Colors ==========*/
    --color-bg: rgba(245, 238, 215, 1);
    --color-text: rgba(115, 106, 81, 1);
    --color-primary: rgba(191, 59, 59, 1);
    --color-secondary: rgba(255, 159, 41, 1);
}

[data-theme="dark"] {
    /*========== Colors ==========*/
    --color-bg: rgba(13, 82, 84, 1);
    --color-text: rgba(241, 241, 241, 1);
    --color-primary: rgba(241, 241, 241, 1);
    --color-secondary: rgba(241, 241, 241, 1);
}

[data-theme="tonic"] {
    /*========== Colors ==========*/
    --color-bg: rgba(253, 255, 252, 1);
    --color-text: rgba(1, 22, 39, 1);
    --color-primary: rgba(187, 13, 36, 1);
    --color-secondary: rgba(4, 80, 142, 1);
}

[data-theme="rainy"] {
    /*========== Colors ==========*/
    --color-bg: rgba(228, 253, 225, 1);
    --color-text: rgba(87, 87, 97, 1);
    --color-primary: rgba(138, 203, 136, 1);
    --color-secondary: rgba(100, 131, 129, 1);
}

*::-webkit-input-placeholder {
    color: #666;
    opacity: 1;
}

*:-moz-placeholder {
    color: #666;
    opacity: 1;
}

*::-moz-placeholder {
    color: #666;
    opacity: 1;
}

*:-ms-input-placeholder {
    color: #666;
    opacity: 1;
}

body input:focus:required:invalid,
body textarea:focus:required:invalid {
    color: #666;
}

body input:required:valid,
body textarea:required:valid {
    color: #666;
}

html, body {
    height: 100%;
    font-size: var(--font-size-base);
}

body {
    min-width: 320px;
    position: relative;
    line-height: 1.75;
    font-family: var(--font-body);
    background-color: var(--color-bg);
    color: var(--color-text);
    overflow-x: hidden;
    opacity: 1;
}

.main {
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    max-height: 100vh;
    max-width: 550px;
    margin: 0 auto;
}

.panel {
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin-top: 5px;
}

/*=============== GEO ===============*/
.geo {
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-around;
    margin-top: 27px;
    font-size: 2rem;
    letter-spacing: -0.05rem;
}

.geo__location {
    padding-left: 6px;
}

.geo__date {
    padding-top: 5px;
}

/*=============== TEMPERATURE ===============*/
.temperature {
    display: flex;
    flex-wrap: nowrap;
    margin: 2.8rem auto 0 auto;
}

.temperature__deg {
    margin-top: 5.9rem;
    letter-spacing: 0.35rem;
    line-height: .7;
    font-family: var(--font-koh);
    font-size: 23rem;
    color: var(--color-primary);
}

.temperature__wrap {
    margin-left: 1.7rem;
}

.temperature__mesure {
    font-size: 4.5rem;
    color: var(--color-secondary);
}

.temperature__box {
    margin-top: 4.2rem;
    margin-left: 0.7rem;
}

.temperature__min, .temperature__max {
    display: flex;
    flex-direction: row;
    align-items: baseline;
    line-height: .8;
    font-size: 1.5rem;
    margin-bottom: 30px;
}

.temperature__mark {
    color: var(--color-secondary);
}

.temperature__text {
    margin-left: .2rem;
}

/*=============== DETAIL ===============*/
.detail {
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
}

.detail__wrap {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
}

.detail__icon {
    margin-left: 2rem;
    font-size: 6rem;
    color: var(--color-secondary);
}

.detail__description {
    flex-basis: 26rem;
    text-transform: uppercase;
    letter-spacing: 0.15rem;
    font-size: 2.7rem;
}

.detail__box {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    margin-top: 0.4rem;
}

.detail__indicator {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-right: 30px;
}

.detail__info {
    font-size: 2.5rem;
}

.detail__measure {
    margin-left: .3rem;
    color: var(--color-secondary);
}

.detail__name {
    letter-spacing: 0.3rem;
}

/*=============== DIAGRAM ===============*/
.diagram {
    max-width: 95%;
    width: 100%;
    margin: 20px auto 0 auto;
}

.search {
    flex-basis: 70%;
    position: relative;
}

.search__wrap {
    position: relative;
    width: 90%;
    margin: 0 auto;
    height: 3.5rem;
    border: 2px solid var(--color-primary);
}

.search__input {
    width: 100%;
    height: 100%;
    padding-left: 10px;
    border: none;
    border-radius: 5px;
    outline: none;
    background: var(--color-bg);
    color: var(--color-text);
}

.search__input::-webkit-input-placeholder {
    color: var(--color-text);
}

.search__input::-ms-input-placeholder {
    color: var(--color-text);
}

.search__input::-moz-placeholder {
    color: var(--color-text);
}

.search__input:-moz-placeholder {
    color: var(--color-text);
}

.search__button {
    position: absolute;
    top: 0;
    right: 0;
    width: 4rem;
    height: 105%;
    border: none;
    background: var(--color-primary);
    cursor: pointer;
}

.search__icon {
    max-width: 100%;
    width: auto;
    height: auto;
}

.search__autocomplete {
    position: absolute;
    list-style-type: none;
    left: -2px;
    top: 3.5rem;
    width: 90%;
    padding: 0;
    margin: 0 auto;
    font-family: var(--font-body);
    border-top: 2px solid var(--color-primary);
    border-right: 2px solid var(--color-primary);
    border-left: 2px solid var(--color-primary);
    border-bottom: 2px solid var(--color-primary);
    z-index: 5;
}

.search__autocomplete-item {
    padding: 0.5rem 1.7rem;
    border-bottom: 2px solid var(--color-bg);
    background-color: var(--color-bg);
    color: var(--color-text);
    cursor: pointer;
    transition: .5s all;
}

.search__autocomplete-item:hover, .search__autocomplete-item--color {
    border-bottom: 2px solid var(--color-primary);
}

.theme-switcher {
    display: flex;
}

.theme-switcher__main {
    margin: auto;
    background-color: transparent;
    cursor: pointer;
}

.theme-switcher__main::before {
    content: ' ';
    display: inline-block;
    width: 2rem;
    height: 2rem;
    vertical-align: middle;
    box-shadow: 0 0 5px var(--color-secondary);
    border: 2px solid var(--color-primary);
    border-radius: 50%;
    background: var(--color-bg);
}

.theme-switcher__dropdown {
    opacity: 1;
    pointer-events: auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    position: absolute;
    top: 2.3rem;
    padding: 0;
    transition: opacity .15s ease-out;
    overflow: hidden;
    border: 2px solid var(--color-primary);
    background: var(--color-bg);
    transform: translateX(-40%);
    z-index: 10;
}

.theme-switcher__dropdown-hide {
    pointer-events: none;
    opacity: 0;
}

.theme-switcher__item {
    margin: .5rem 1.4rem;
    list-style-type: none;
}

.theme-switcher__toggle {
    display: inline-block;
    width: 2rem;
    height: 2rem;
    vertical-align: middle;
    box-shadow: 0 0 5px var(--color-secondary);
    border: 2px solid var(--color-primary);
    border-radius: 50%;
    background: var(--color-bg);
    cursor: pointer;
}

/*==========  Mobile First  ==========*/
/* Custom, iPhone Retina */
@media only screen and (min-width: 320px) {
    html {
        font-size: 8px;
    }
}

/* Extra Small Devices, Phones */
@media only screen and (min-width: 480px) {
    html {
        font-size: 9px;
    }
}

/* Small Devices, Tablets */
@media only screen and (min-width: 768px) {
    html {
        font-size: 10px;
    }
}

/* Medium Devices, Desktops */
@media only screen and (min-width: 992px) {
    html {
        font-size: 11px;
    }
}

/* Large Devices, Wide Screens */
@media only screen and (min-width: 1200px) {
    html {
        font-size: 12px;
    }
}
